.App {
  height: 100vh;
  max-width: 100vw;
  position: relative;
  font-family: Arial, Helvetica, sans-serif;
}

.App-logo {
  height: 200px;
  width: 200px;
}

.App-header {
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-content: center;
  align-items: center;
  font-size: 18px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
h1 {
  color: red;
}

hr {
  border-top: 2px solid red;
  margin: 0px;
}

.App-body {
  height: 45%;
  font-size: 14px;
  margin-top: 50px;
  margin-left: 40px;
  
}
.App-footer p {
  font-style: italic;
  font-size: 1rem;
  text-align: center;
}

form input {
  margin: 5px;
}
